.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.chart-layout {
  width: 100%;
}

.chart-settings {
  display: flex;
  column-gap: 20px;
}

.chart {
  display: flex;
  margin-top: 40px;
  padding: 0px 50px 0px 50px;
}

.selection-menu {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  width: 150px;
}

.removable {
  text-align: left;
  width: 200px;
}

.removable:hover {
  background-color: red;
  border-radius: 5px;
}

.start-button {
  width: 100px;
  height: 50px;
}